const { LogLevel } = require('./log-levels');

const state = {
  impl:  null,
  level: LogLevel.INFO,
};

const LogBinding = {
  getImpl() {
    return state.impl;
  },

  setImpl(impl) {
    state.impl = impl;
  },

  getLogLevel() {
    return state.level;
  },

  setLogLevel(level) {
    state.level = level;
  },
};

module.exports.GlobalBinding = LogBinding;



